<template>
    <el-table
            :data="tableUserInfo"
            style="width: 100%"
            height="90%">
        <el-table-column
                fixed
                :key="col"
                :prop="col"
                :label="col"
                width="150"
                v-for="col in cols">
        </el-table-column>
        <el-table-column
                fixed="right"
                label="操作"
                width="100">
            <template slot-scope="scope">
                <el-button @click="delUserInfoClick(scope.row)" type="text" size="small">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
</template>

<script>
    import axios from "axios";
    import qs from "qs";

    export default {
        name: "ShowUser",
        data() {
            return {
                tableUserInfo: [],
                cols: []
            }
        },
        mounted() {
            console.log("mounted")
            this.getTableUserInfo()
        },
        methods: {
            //从后台获取数据源信息
            getTableUserInfo() {
                axios.post('http://localhost:8888/getTableUserInfo')
                    .then((response) => {
                            console.log(response)
                            this.cols = Object.keys(response.data[0])
                            console.log(this.cols)
                            this.tableUserInfo = response.data
                        }, (error) => {
                            this.$message.error('查询失败' + error.response.data.msg)
                        }
                    )
            },
            //删除用户信息
            delUserInfoClick(row) {
                console.log(row.userId);
                axios.post('http://localhost:8888/delByUserId', qs.stringify(
                    {
                        userId: row.userId
                    }))
                    .then((response) => {
                            console.log(response)
                            this.getTableUserInfo()
                        }
                    )
            }
        }
    }
</script>

