<template>
    <div style="text-align: center">
      <p style="font-size: 20px ; width:100%;height: 60px;display: block;line-height: 60px;text-align: center">添加用户</p>
      <el-form label-position="left" label-width="80px" :model="formUserInfo">
        <el-form-item label="用户编号">
          <el-input v-model="formUserInfo.userId"></el-input>
        </el-form-item>
        <el-form-item label="用户名">
          <el-input v-model="formUserInfo.userName"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="formUserInfo.userPassword" show-password></el-input>
        </el-form-item>
        <el-form-item label="确认">
          <el-input v-model="repeat" show-password></el-input>
        </el-form-item>
        <el-form-item label="身份">
          <el-select v-model="formUserInfo.userIdentity" placeholder="请选择身份">
            <el-option label="中间件管理者" value="manager"></el-option>
            <el-option label="数据集成者" value="dataIntegrator"></el-option>
            <el-option label="软件开发者" value="developer"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">添加</el-button>
          <el-button>取消</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'AddUser',
  data () {
    return {
      formUserInfo: {
        userId:'',
        userName: '',
        userPassword: '',
        userIdentity: ''
      },
      repeat: '',
    }
  },
  methods: {
    onSubmit () {
      console.log(this.formUserInfo)
      if(this.formUserInfo.userId===''||this.formUserInfo.userName===''||this.formUserInfo.userPassword===''
              || this.formUserInfo.userIdentity==='')
      {
        alert("用户信息不能为空！")
        return
      }
      if(this.formUserInfo.userPassword!==this.repeat)
      {
        alert("两次输入密码不一致")
        return
      }
      axios.post('http://localhost:8888/addUserInfo',this.formUserInfo)
              .then((response)=>{
                        console.log(response);
                        alert("添加用户信息成功")
                      }
              )
    }
  }
}
</script>

